import { MIN_PRICE, MAX_PRICE } from './const';
import dayjs from 'dayjs';

const DATE_FORMAT = 'D MMM';

/**
 * ÐÐ¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÐ»ÑÑÐ°Ð¹Ð½Ð¾Ðµ ÑÐµÐ»Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð² Ð´Ð¸Ð°Ð¿Ð°Ð·Ð¾Ð½Ðµ Ð¾Ñ min Ð´Ð¾ max
 * @param {Number} min Ð¦ÐµÐ»Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð½Ð¸Ð¶Ð½ÐµÐ¹ Ð³ÑÐ°Ð½Ð¸ÑÑ Ð³ÐµÐ½ÐµÑÐ°ÑÐ¸Ð¸
 * @param {Number} max Ð¦ÐµÐ»Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð²ÐµÑÑÐ½ÐµÐ¹ Ð³ÑÐ°Ð½Ð¸ÑÑ Ð³ÐµÐ½ÐµÑÐ°ÑÐ¸Ð¸
 * @returns Ð¡Ð»ÑÑÐ°Ð¹Ð½Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð² Ð´Ð¸Ð°Ð¿Ð°Ð·Ð¾Ð½Ðµ Ð¾Ñ min Ð´Ð¾ max
 */
const getRandomInteger = (min = MIN_PRICE, max = MAX_PRICE) => Math.floor(Math.random() * (max - min + 1)) + min;

/**
 * ÐÐ¾Ð»ÑÑÐ°ÐµÑ ÑÐ»ÑÑÐ°Ð¹Ð½ÑÐ¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½Ð¾Ð³Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ð°
 * @param {Array} items ÐÐ°ÑÑÐ¸Ð², ÑÐ»ÑÑÐ°Ð¹Ð½ÑÐ¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ ÐºÐ¾ÑÐ¾ÑÐ¾Ð³Ð¾ Ð½Ðµ Ð¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð¾ Ð¿Ð¾Ð»ÑÑÐ¸ÑÑ
 * @returns {any} Ð¡Ð»ÑÑÐ°Ð¹Ð½ÑÐ¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ Ð¼Ð°ÑÑÐ¸Ð²Ð°
 */
const getRandomArrayElement = (items) => items[getRandomInteger(0, items.length - 1)];

/**
 * ÐÐ°Ð´Ð°ÑÑ ÑÐµÐ»Ð¾Ð²ÐµÐºÐ¾Ð¿Ð¾Ð½ÑÑÐ½ÑÐ¹ ÑÐ¾ÑÐ¼Ð°Ñ Ð´Ð°ÑÑ ÑÐ¾ÑÐºÐ¸ ÑÐ¾Ð±ÑÑÐ¸Ñ
 * @param {Date} eventDate - ÐÐ°ÑÐ° ÑÐ¾ÑÐºÐ¸ ÑÐ¾Ð±ÑÑÐ¸Ñ
 * @returns {string} Ð§ÐµÐ»Ð¾Ð²ÐµÐºÐ¾Ð¿Ð¾Ð½ÑÑÐ½ÑÑ Ð´Ð°ÑÑ
 */
const humanizeEventDate = (eventDate) => eventDate ? dayjs(eventDate).format(DATE_FORMAT) : '';

/**
 * Ð¤Ð¾ÑÐ¼Ð°ÑÐ¸ÑÑÐµÑ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÑ Ð´Ð°ÑÑ Ð¿ÑÐ¸ Ð¿Ð¾Ð¼Ð¾ÑÐ¸ dayjs Ð¿Ð¾ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½Ð¾Ð¼Ñ ÑÐ¾ÑÐ¼Ð°ÑÑ
 * @param {Date} date ÐÐ°ÑÐ° Ð´Ð»Ñ ÑÐ¾ÑÐ¼Ð°ÑÐ¸ÑÐ¾Ð²Ð°Ð½Ð¸Ñ
 * @param {string} format Ð¤Ð¾ÑÐ¼Ð°Ñ Ð´Ð»Ñ Ð½Ð¾Ð²Ð¾Ð¹ Ð´Ð°ÑÑ
 * @returns {string} ÐÐ¾Ð²Ð°Ñ Ð¾ÑÑÐ¾ÑÐ¼Ð°ÑÐ¸ÑÐ¾Ð²Ð°Ð½Ð½Ð°Ñ Ð´Ð°ÑÐ°
 */
const formatDate = (date, format) => dayjs(date).format(format);

/**
 * ÐÑÐ²Ð¾Ð´Ð¸Ñ ÑÐ°Ð·Ð½Ð¸ÑÑ Ð² Ð´Ð²ÑÑ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÑ Ð´Ð°ÑÐ°Ñ Ð¿Ð¾ Ð¿ÑÐ¸Ð½ÑÐ¸Ð¿Ñ:
 * Ð¼ÐµÐ½ÐµÐµ ÑÐ°ÑÐ° â Ð¼Ð¸Ð½ÑÑÑ;
 * Ð¼ÐµÐ½ÐµÐµ ÑÑÑÐ¾Ðº â ÑÐ°ÑÑ Ð¼Ð¸Ð½ÑÑÑ;
 * Ð±Ð¾Ð»ÐµÐµ ÑÑÑÐ¾Ðº â Ð´Ð½Ð¸ ÑÐ°ÑÑ Ð¼Ð¸Ð½ÑÑÑ.
 * @param {Date} dateFrom ÐÐ°ÑÐ° Ð½Ð°ÑÐ°Ð»Ð° ÑÐ¾Ð±ÑÑÐ¸Ñ
 * @param {Date} dateTo ÐÐ°ÑÐ° ÐºÐ¾Ð½ÑÐ° ÑÐ¾Ð±ÑÑÐ¸Ñ
 * @returns Ð Ð°Ð·Ð½Ð¸ÑÐ° Ð¼ÐµÐ¶Ð´Ñ Ð´Ð²ÑÐ¼Ñ Ð´Ð°ÑÐ°Ð¼Ð¸
 */
const calculateDifference = (dateFrom, dateTo) => {
  const start = dayjs(dateFrom);
  const end = dayjs(dateTo);
  const diffInMinutes = end.diff(start, 'minute');
  let minutes;
  if (diffInMinutes < 60) {
    return `${diffInMinutes}M`;
  }

  const diffInHours = end.diff(start, 'hour');
  if (diffInHours < 24) {
    minutes = diffInMinutes % 60;
    return `${diffInHours}H ${minutes}M`;
  }

  const diffInDays = end.diff(start, 'day');
  const hours = diffInHours % 24;
  minutes = diffInMinutes % 60;
  return `${diffInDays}D ${hours}H ${minutes}M`;
};

/**
 * ÐÐ¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ Ð¸ÑÐºÐ¾Ð¼ÑÐ¹ Ð¿Ð¾ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½Ð¾Ð¼Ñ ID Ð³Ð¾ÑÐ¾Ð´ Ð¸Ð· Ð¼Ð°ÑÑÐ¸Ð²Ð° Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÑ Ð¶Ðµ Ð³Ð¾ÑÐ¾Ð´Ð¾Ð²
 * @param {Number} id ID Ð¸ÑÐºÐ¾Ð¼Ð¾Ð³Ð¾ Ð³Ð¾ÑÐ¾Ð´Ð°
 * @param {Array} cities ÐÐ°ÑÑÐ¸Ð² Ð¾Ð±ÑÐµÐºÑÐ¾Ð² Ð³Ð¾ÑÐ¾Ð´Ð¾Ð²
 * @returns {Object} ÐÐ±ÑÐµÐºÑ Ð¸ÑÐºÐ¾Ð¼Ð¾Ð³Ð¾ Ð³Ð¾ÑÐ¾Ð´Ð°
 */
const getCityInfoByID = (id, cities) => cities.find((city) => city.id === id);

const getOfferInfoById = (id, offers, type) => {
  const offersByType = offers.find((offer) => offer.type.toLowerCase() === type.toLowerCase());

  if (offersByType && offersByType !== 'undefined') {
    return offersByType.offers.find((offer) => offer.id === id);
  }

  return null;
};

const capitalizeFirstLetter = (word) => {
  if (!word) {
    return '';
  }
  return word.charAt(0).toUpperCase() + word.slice(1);
};

export {getRandomArrayElement, getRandomInteger, humanizeEventDate, formatDate, calculateDifference, getCityInfoByID, getOfferInfoById, capitalizeFirstLetter };
